import React, { useState } from "react";
import PropTypes from "prop-types";
import { Panel, Div, PanelHeader } from "@vkontakte/vkui";
import "./MainPage.css";
import { ArrowUpIcon } from "../../icons/ArrowUpIcon";

export const MainPage = ({ id, go, answers }) => {
  console.log(answers);
  const [state, setState] = useState({
    money: 0,
    riskValue: answers.percentage,
    lastDiffRiskValue: answers.lastDiffRiskValue,
    challenges: answers.challenges,
    recommendedChallenges: [
      {
        title: "Не курить",
        diffRiskValue: 2,
        days: 30,
        daysLeft: 30,
        description: "Blablabla",
        id: 1
      }
    ]
  });

  const percentage = answers.percentage;

  const borderColor = `rgb(${percentage / 0.51}%, ${100 -
    percentage / 0.51}%, 65%)`;

  const getDate = () => {
    const date = new Date();
    const month = date.getMonth() + 1;
    const dayOfMonth = date.getDate();
    const dayOfWeek = getDayOfWeek(date.getDay());

    return `${dayOfWeek} ${dayOfMonth}.${month}`;
  };

  const getDayOfWeek = dayNumber => {
    switch (dayNumber) {
      case 0:
        return "Вс";
      case 1:
        return "Пн";
      case 2:
        return "Вт";
      case 3:
        return "Ср";
      case 4:
        return "Чт";
      case 5:
        return "Пт";
      case 6:
        return "Сб";
    }
  };

  return (
    <Panel id={id}>
      <PanelHeader>Главная</PanelHeader>
      <Div className="main-page">
        <Div className="main-page__header">
          <Div className="main-page__header-inner">
            <Div className="main-page__header-money">{`${state.money}₽`}</Div>
            <Div className="main-page__diagram-wrapper">
              <Div className="main-page__diagram" style={{ borderColor }}>
                {`${parseFloat(percentage).toFixed(1)}%`}
                <br />
                <Div className="main-page__diagram-dynamic">
                  <ArrowUpIcon
                    className={`main-page__diagram-dynamic-icon ${state.lastDiffRiskValue <
                      0 && "main-page__diagram-dynamic-icon-rotated"}`}
                  />
                  {`${parseFloat(state.lastDiffRiskValue).toFixed(1)}%`}
                </Div>
              </Div>
            </Div>
          </Div>

          <Div className="main-page__header-text">
            вы помогли собрать в фонд ОРБИ
          </Div>

          <Div className="main-page__challenges">
            <Div className="main-page__challenges-header">
              <Div className="main-page__challenges-header-text">Челенжди</Div>
              <Div className="main-page__challenges-header-date">
                {getDate()}
              </Div>
            </Div>
            <Div className="main-page__challenges-main">
              {Boolean(state.challenges && state.challenges.length)
                ? state.challenges.map((challenge, key) => {
                    return (
                      <Div
                        className="main-page__challenges-challenge"
                        key={key}
                      >
                        {challenge.title}
                      </Div>
                    );
                  })
                : "У Вас нет активных челенджей, можете добавить их из списка рекомендуемых."}
            </Div>
          </Div>

          <Div className="main-page__recommended">
            <Div className="main-page__recommended-header">
              <Div className="main-page__recommended-header-text">
                Рекомендуемые
              </Div>
            </Div>
            <Div className="main-page__recommended-main">
              {Boolean(
                state.recommendedChallenges &&
                  state.recommendedChallenges.length
              )
                ? state.recommendedChallenges.map((challenge, key) => {
                    return (
                      <Div
                        className="main-page__recommended-challenge"
                        key={key}
                      >
                        <div className="main-page__recommended-challenge-title">
                          {challenge.title}
                        </div>
                        <div className="main-page__recommended-challenge-value">
                          <div>{`-${challenge.diffRiskValue}%`}</div>от риска
                        </div>
                      </Div>
                    );
                  })
                : "К сожалению мы не смогли подобрать для Вас челенжди. Пожалуйста, попробуйте зайти позднее"}
            </Div>
          </Div>
        </Div>
      </Div>
    </Panel>
  );
};

MainPage.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string
    })
  }),
  answers: PropTypes.object
};
